<template>
  <div class="card">
    <van-row justify="center">
      <van-col span="13">
        <h2></h2>
        <el-table
                :data="orderInfo"
                max-height="800"
                stripe
                border
                highlight-current-row
                style="width: 100%">
          <el-table-column
                  prop="orderNo"
                  label=""
                  width="180">
          </el-table-column>
          <el-table-column
                  prop="crane"
                  label=""
                  width="180">
          </el-table-column>
          <el-table-column
                  prop="start"
                  label="">
          </el-table-column>
          <el-table-column
                  prop="end"
                  label="">
          </el-table-column>
          <el-table-column
                  prop="coilNo"
                  label="">
          </el-table-column>
          <el-table-column
                  prop="startTime"
                  label="">
          </el-table-column>
        </el-table>
      </van-col>
    </van-row>
  </div>
</template>

<script>
  import {onMounted, reactive, toRefs, ref, watchEffect, watch} from 'vue';
  import {useStore} from 'vuex';

  export default {
    name: "Order",
    setup() {
      const store = useStore();
      const state = reactive({
        orderInfo: [],
      });
      onMounted(() => {
        state.orderInfo = store.getters.getOrderInfo;
        for (let order of state.orderInfo) {
          order.start = order.start.join('-');
          order.end = order.end.join('-');
        }
        console.log(state.orderInfo);
        // console.log(state.orderInfo[0].keys());
      });
      return {
        ...toRefs(state),
      }
    },
  }
</script>

